<template>
  <div class="table-data-container">
    <div class="table-data-search">
      <div class="search">
        <div class="search-header">
          <h3>Панель поиска</h3>
          <div class="radio-show-filter">
            <v-icon
                color="blue-grey-darken-2"
                size="small"
            >mdi-filter-remove
            </v-icon>
            <v-switch
                v-model="isRadioResult"
                color="primary"
                hide-details
                class="switch-filter"
            ></v-switch>
            <v-icon
                color="blue-grey-darken-2"
                size="small"
            >mdi-filter
            </v-icon>
          </div>
        </div>
        <div class="search-input">
          <v-text-field v-model="inputData" label="Поиск..." color="teal" density="compact"
                        variant="underlined" clearable></v-text-field>
          <v-btn class="apply-search-btn"
                 color="teal"
                 size="small"
                 @click="searchGoods"
                 :disabled="isSearchOrFiltered"
          >
            <v-icon>mdi-magnify</v-icon>
            <span>Искать</span></v-btn>
        </div>
        <div v-show="isFilterShow" class="search-select">

          <v-autocomplete
              v-model="searchData.category"
              clearable
              label="Категория"
              :items="goodsCategory"
              multiple
              variant="underlined"
              density="compact"
              color="teal"
              persistent-hint
              class="select-input">
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.category.length - 4 }} еще)
      </span>
            </template>
          </v-autocomplete>

          <v-autocomplete
              v-model="searchData.collection"
              :items="goodsCollection"
              label="Коллекция"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable>
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.collection.length - 4 }} еще)
      </span>
            </template>
          </v-autocomplete>

          <v-autocomplete
              v-model="searchData.tm"
              :items="goodsTM"
              label="Торговая марка"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.tm.length - 4 }} еще)
      </span>
            </template>
          </v-autocomplete>

          <v-autocomplete
              v-model="searchData.typeOfcount"
              :items="goodsNumerousCount"
              label="Ед. измерения"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.typeOfcount.length - 4 }} еще)
      </span>
            </template>
          </v-autocomplete>

          <v-select
              v-model="searchData.typeOfRrc"
              :items="goodsSalePersentage"
              label="Тип РРЦ"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.typeOfRrc.length - 4 }} еще)
      </span>
            </template>
          </v-select>

          <v-select
              v-model="searchData.status"
              :items="goodsStatus"
              label="Статус товара"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.status.length - 4 }} еще)
      </span>
            </template>
          </v-select>

          <v-select
              v-model="searchData.stock"
              :items="goodsStock"
              label="Сток"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.stock.length - 4 }} еще)
      </span>
            </template>
          </v-select>

          <v-select
              v-model="searchData.available"
              :items="goodsCount"
              label="Наличие на складе"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.available.length - 4 }} еще)
      </span>
            </template>
          </v-select>

          <v-select
              v-model="searchData.reserve"
              :items="goodsReserves"
              label="Наличие на резерве"
              multiple
              persistent-hint
              density="compact"
              color="teal"
              class="select-input"
              variant="underlined"
              clearable
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index < 2"
                      style="height: 18px; margin-bottom: 2px; line-height: 18px; font-size: 12px"
                      color="warning"
              ><span>{{ item.title }}</span>
              </v-chip>
              <span
                  v-if="index === 2"
                  class="text-grey text-caption align-self-center"
              >
        (+{{ checkLengthValeu.length + searchData.reserve.length - 4 }} еще)
      </span>
            </template>
          </v-select>

          <v-btn
              class="apply-filter-btn"
              color="teal" size="small"
              @click="filterGoods"
              :disabled="isSearchOrFiltered"
          >
            <v-icon>mdi-filter</v-icon>
            <span>Фильтровать</span></v-btn>
        </div>
        <div class="buttons-area">
          <div class="buttons-left">
            <v-btn
                v-if="!isPromoter"
                class="apply-filter-btn in-table-btn"
                color="deep-purple" size="small"
                @click="createFullTableData"
                :disabled="!tableGenerationButton"
            >
              <v-icon>mdi-table-edit</v-icon>
              <span>Вывести всё</span></v-btn>
            <v-btn
                v-if="!isPromoter"
                class="apply-filter-btn in-table-btn"
                color="purple" size="small"
                @click="createCurrentTableData"
                :disabled="!tableGenerationButton"
            >
              <v-icon>mdi-table-edit</v-icon>
              <span>Вывести текущее</span></v-btn>
          </div>
          <div class="buttons-right">
            <v-btn
                class="apply-filter-btn filter-btn"
                color="red" size="small"
                @click="newGenerationData"
                :disabled="!newGenerationButton"
            >
              <v-icon>mdi-label-outline</v-icon>
              <span>Новинки</span></v-btn>
            <v-btn
                class="apply-filter-btn filter-btn"
                color="success" size="small"
                @click="allGenerationData"
                :disabled="!allGenerationButton"
            >
              <v-icon>mdi-download</v-icon>
              <span>Загрузить всё</span></v-btn>
            <v-btn
                class="apply-filter-btn filter-btn"
                color="warning" size="small"
                @click="firstGenerationClearData"
                :disabled="!firstGenerationButton"
            >
              <v-icon>mdi-cached</v-icon>
              <span>Очистить</span></v-btn>
          </div>
        </div>
      </div>
      <div class="table" v-show="!isSearchOrFiltered">
        <div class="table-header" v-show="!isItemShow">
          <div class="table-header__photo">Фото</div>
          <div class="table-header__code">
            <div class="table-header__title">Код</div>
            <div class="table-header__sort">
              <v-icon @click="codeUp" size="x-small" style="padding-top: 5px">mdi-apple-keyboard-control</v-icon>
              <v-icon @click="codeDown" size="x-small" style="transform: rotateZ(180deg); padding-top: 5px">
                mdi-apple-keyboard-control
              </v-icon>
            </div>
          </div>
          <div class="table-header__article">
            <div class="table-header__title">Артикул</div>
            <div class="table-header__sort">
              <v-icon @click="articleUp" size="x-small" style="padding-top: 5px">mdi-apple-keyboard-control</v-icon>
              <v-icon @click="articleDown" size="x-small" style="transform: rotateZ(180deg); padding-top: 5px">
                mdi-apple-keyboard-control
              </v-icon>
            </div>
          </div>
          <div class="table-header__title">
            <div class="table-header__title">Наименование</div>
            <div class="table-header__sort">
              <v-icon @click="titleleUp" size="x-small" style="padding-top: 5px">mdi-apple-keyboard-control</v-icon>
              <v-icon @click="titleleDown" size="x-small" style="transform: rotateZ(180deg); padding-top: 5px">
                mdi-apple-keyboard-control
              </v-icon>
            </div>
          </div>
          <div class="table-header__teg">Тег</div>
          <div class="table-header__available">
            <div class="table-header__title">Остаток</div>
            <div class="table-header__sort">
              <v-icon @click="availableUp" size="x-small" style="padding-top: 5px">mdi-apple-keyboard-control</v-icon>
              <v-icon @click="availableDown" size="x-small" style="transform: rotateZ(180deg); padding-top: 5px">
                mdi-apple-keyboard-control
              </v-icon>
            </div>
          </div>
          <div class="table-header__reserve">
            <div class="table-header__title">Резерв</div>
            <div class="table-header__sort">
              <v-icon @click="reserveUp" size="x-small" style="padding-top: 5px">mdi-apple-keyboard-control</v-icon>
              <v-icon @click="reserveDown" size="x-small" style="transform: rotateZ(180deg); padding-top: 5px">
                mdi-apple-keyboard-control
              </v-icon>
            </div>
          </div>
          <div class="table-header__controls">Управление</div>
          <div class="scrollbar"></div>
        </div>
        <div class="table-data" ref="tableData" v-show="!isItemShow" @scroll="handleScroll">
          <div v-for="good of finalDisplayData" :key="good.Code" class="table-body">
            <div class="table-body__photo">
              <img width="55" height="55" alt="photo" :src="getPhotoPath(good.Article)">
            </div>
            <div class="table-body__code">{{ good.Code }}</div>
            <div class="table-body__article">{{ good.Article }}</div>
            <div class="table-body__title">{{ good.Title }}</div>
            <div class="table-body__teg">
              <v-chip class="chip-new"
                      v-if="good.Status == 'Новинка'"
                      variant="flat"
                      size="x-small"
                      color="pink"
                      label>
                <v-icon icon="mdi-tag" start></v-icon>
                Новинка
              </v-chip>
              <v-chip class="chip-stock"
                      v-if="good.Stock == 'Сток'"
                      variant="flat"
                      size="x-small"
                      color="yellow"
                      label>
                <v-icon icon="mdi-cube-outline" start></v-icon>
                Сток
              </v-chip>
              <v-chip class="chip-permanent"
                      v-if="good.Status != 'Новинка' && good.Stock != 'Сток'"
                      variant="flat"
                      size="x-small"
                      color="green-lighten-4"
                      label>
                <v-icon icon="mdi-cart-outline" start></v-icon>
                Перманент
              </v-chip>
            </div>
            <div class="table-body__available" :style="{'color': good.Available > 0 ? 'green' : 'red'}">
              {{ good.Available }}
            </div>
            <div class="table-body__reserve" :style="{'color': good.Reserv > 0 ? 'orange' : 'red'}">{{
                good.Reserv
              }}
            </div>
            <div class="table-body__controls">
              <v-btn class="controls__details" size="x-small" color="teal" @click="showCurrentItem(good)">Детали</v-btn>
              <v-btn class="controls__choose" size="x-small" color="blue-grey-darken-1" @click="operateItem(good)">
                Выбрать
              </v-btn>
            </div>

            <div class="table-body__available-small" :style="{'color': good.Available > 0 ? 'green' : 'red'}">
              {{ good.Available }}
            </div>
            <div class="table-body__reserve-small" :style="{'color': good.Reserv > 0 ? 'orange' : 'red'}">{{
                good.Reserv
              }}
            </div>
          </div>
        </div>
        <div v-show="isItemShow">
          <ItemComponent
              @closeItem="closeCurrentItem"
              :currentItem="sendItemData"
              :currentReserves="uniqReserves"
              :isVisible="isItemShow"
          />
        </div>
      </div>
      <v-progress-circular
          class="for-search-or-filter"
          :size="60"
          width="6"
          color="blue-darken-2"
          indeterminate
          v-if="isSearchOrFiltered"
      ></v-progress-circular>
    </div>
  </div>
  <div class="text-center">
    <v-snackbar
        class="snack"
        v-model="snackbar"
        multi-line
        timeout="3000"
        style="margin-bottom: 60px"
    >
      <v-icon :color="snackbarResultColor" size="x-large" style="margin-right: 10px">{{ snackbarResult }}</v-icon>
      {{ snackbarText }}
      <template v-slot:actions>
        <v-btn
            color="red"
            variant="text"
            @click="snackbar = false"
        >
          Закрыть
        </v-btn>
      </template>
    </v-snackbar>
  </div>
  <div class="text-center">
    <v-dialog
        v-model="dialog"
        width="auto"
    >
      <v-card
          max-width="400"
          prepend-icon="mdi-alert"
          text="Проверьте корректность введенных данных."
          title="Ничего не найдено"
      >
        <template v-slot:actions>
          <v-btn
              color="red"
              class="ms-auto"
              text="Закрыть"
              @click="dialog = false"
          ></v-btn>
        </template>
      </v-card>
    </v-dialog>
  </div>
  <v-overlay class="overlay-for-data" style="background-color: rgba(0,0,0,0.7)" v-model="overlay">
    <v-sheet
        class="pa-4 text-center mx-auto sheet-for-overlay"
        elevation="12"
        rounded="lg"
    >
      <div class="sheet-buttons">
        <v-btn class="sheet-buttons-btn" color="success" size="small" @click="copyTable">
          <v-icon>mdi-content-copy</v-icon>
          Копировать таблицу
        </v-btn>
        <v-btn class="sheet-buttons-btn" color="red" size="small" @click="overlay = !overlay;">
          <v-icon>mdi-close</v-icon>
          Закрыть
        </v-btn>
      </div>
      <table ref="copyData">
        <thead>
        <tr>
          <th>Код</th>
          <th>Артикул</th>
          <th>Артикул ЕПЦ</th>
          <th>Штрихкод</th>
          <th>УКТЗ</th>
          <th>Наименование</th>
          <th>Цена (база)</th>
          <th>РРЦ</th>
          <th>РРЦ (акция)</th>
          <th>% скидки</th>
          <th>Склад</th>
          <th>ДПМ</th>
          <th>2320</th>
          <th>Резерв</th>
          <th>В коробке</th>
          <th>Акционирование</th>
          <th>Категоризация</th>
          <th>Заказ (шт)</th>
          <th>Заказ (дата)</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="good of copyTableData" :key="good.Code">
          <td>{{ good.Code }}</td>
          <td>{{ good.Article }}</td>
          <td>{{ good.ArtEpicenter }}</td>
          <td>{{ good.Shtrihcode }}</td>
          <td>{{ good.Uktz }}</td>
          <td class="td-title">{{ good.Title }}</td>
          <td>{{ good.PriceBase.toString().replace(".", ",") }}</td>
          <td>{{ Number(good.PriseRrc).toFixed(2).toString().replace(".", ",") }}</td>
          <td>{{ good.PriseRrcPromo.toString().replace(".", ",") }}</td>
          <td>{{ (good.PriceDiscount * 100).toFixed() + '%' }}</td>
          <td>{{ good.Available }}</td>
          <td>{{ good.AvailableDpm }}</td>
          <td>{{ good.Available2320 }}</td>
          <td>{{ good.Reserv }}</td>
          <td>{{ good.CountBox }}</td>
          <td>{{ good.Akcion }}</td>
          <td>{{ good.Categor }}</td>
          <td>{{ good.OrderCount }}</td>
          <td>{{ good.OrderData }}</td>
        </tr>
        </tbody>
      </table>
    </v-sheet>
  </v-overlay>
</template>

<script>
import ItemComponent from "@/components/body-search/ItemComponent.vue";
import {dataStore} from "@/operations/requests";

export default {
  name: "TableDataComponent",
  components: {ItemComponent},
  data() {
    return {
      overlay: false,
      screenWidth: window.innerWidth,
      isPromoter: false,

      goodsCategory: [],
      goodsCollection: [],
      goodsTM: [],
      goodsNumerousCount: [],
      goodsSalePersentage: [],
      goodsStatus: [],
      goodsStock: [],
      goodsCount: [],
      goodsReserves: [],
      checkLengthValeu: ['1', '2'],

      inputData: '',
      searchData: {
        category: [],
        collection: [],
        tm: [],
        typeOfcount: [],
        typeOfRrc: [],
        status: [],
        stock: [],
        available: [],
        reserve: [],
      },
      isSearchOrFiltered: false,
      isFilterShow: true,
      isRadioResult: true,
      firstGenerationButton: true,
      allGenerationButton: true,
      newGenerationButton: true,
      tableGenerationButton: true,

      goodsFromProps: [],
      reservesFromProps: [],
      showTableData: [],
      finalDisplayData: [],
      copyTableData: [],
      uniqReserves: [],

      itemsPerLoad: 50,
      loadedCount: 0,

      isItemShow: false,
      sendItemData: {},

      snackbar: false,
      snackbarText: '',
      snackbarResult: '',
      snackbarResultColor: '',

      dialog: false,
    }
  },
  methods: {
    tranferGoodsAndReservesFromProps() {
      this.goodsFromProps = [...dataStore.getGoodData()];
      this.reservesFromProps = [...dataStore.getReservData()];
      this.addReservesToGoods();
      this.firstGenerationClearData();
    },
    addReservesToGoods() {
      const uniqueReserv = Object.values(
          this.reservesFromProps.reduce((acc, curr) => {
            if (!acc[curr.CodeReserv]) {
              acc[curr.CodeReserv] = { CodeReserv: curr.CodeReserv, CountReserv: Number(curr.CountReserv) };
            } else {
              acc[curr.CodeReserv].CountReserv += Number(curr.CountReserv);
            }
            return acc;
          }, {})
      );

      this.goodsFromProps = this.goodsFromProps.map(item => {
        const reserve = uniqueReserv.find(r => r.CodeReserv === item.Code);
        return {
          ...item,
          Reserv: reserve ? reserve.CountReserv : 0,
        };
      });
      dataStore.setGoodAndReservData(this.goodsFromProps);
    },
    createSelectedData() {
      this.goodsCategory = [...new Set(this.goodsFromProps.map(item => item.Kategori).sort((a, b) => a.localeCompare(b)))];
      this.goodsCollection = [...new Set(this.goodsFromProps.map(item => item.Kollection).sort((a, b) => a.localeCompare(b)))];
      this.goodsTM = [...new Set(this.goodsFromProps.map(item => item.Tm).sort((a, b) => a.localeCompare(b)))];
      this.goodsNumerousCount = [...new Set(this.goodsFromProps.map(item => item.Unit).sort((a, b) => a.localeCompare(b)))];
      this.goodsSalePersentage = ['РРЦ обычное', 'РРЦ акционное'];
      this.goodsStatus = [...new Set(this.goodsFromProps.map(item => item.Status).sort((a, b) => a.localeCompare(b)))];
      this.goodsStock = [...new Set(this.goodsFromProps.map(item => item.Stock).sort((a, b) => a.localeCompare(b)))];
      this.goodsCount = ['Есть', 'Нет'];
      this.goodsReserves = ['Есть', 'Нет'];
    },

    filterGoods() {
      this.disabledBTN();
      if (this.isItemShow) {
        this.isItemShow = false;
      }

      setTimeout(() => {
        const filters = [
          (good) => !this.searchData.category.length || this.searchData.category.includes(good.Kategori),
          (good) => !this.searchData.collection.length || this.searchData.collection.includes(good.Kollection),
          (good) => !this.searchData.tm.length || this.searchData.tm.includes(good.Tm),
          (good) => !this.searchData.typeOfcount.length || this.searchData.typeOfcount.includes(good.Unit),
          (good) =>
              !this.searchData.typeOfRrc.length ||
              (this.searchData.typeOfRrc.includes('РРЦ акционное') && Number(good.PriseRrcPromo) > 0) ||
              (this.searchData.typeOfRrc.includes('РРЦ обычное') && Number(good.PriseRrc) > 0),
          (good) =>
              !this.searchData.status.length ||
              (this.searchData.status.includes('Новинка') && good.Status === 'Новинка') ||
              (this.searchData.status.includes('Постоянный ассортимент') && good.Status === 'Постоянный ассортимент'),
          (good) =>
              !this.searchData.stock.length ||
              (this.searchData.stock.includes('Сток') && good.Stock === 'Сток') ||
              (this.searchData.stock.includes('Не сток') && good.Stock === 'Не сток'),
          (good) =>
              !this.searchData.available.length ||
              (this.searchData.available.includes('Есть') && (Number(good.Available) + Number(good.AvailableDpm)) > 0) ||
              (this.searchData.available.includes('Нет') && (Number(good.Available) + Number(good.AvailableDpm)) === 0),
          (good) =>
              !this.searchData.reserve.length ||
              (this.searchData.reserve.includes('Есть') && Number(good.Reserv) > 0) ||
              (this.searchData.reserve.includes('Нет') && Number(good.Reserv) === 0),
        ];

        // Фильтрация данных через все условия
        const filteredGoods = this.goodsFromProps.filter((good) =>
            filters.every((filter) => filter(good))
        );

        if (filteredGoods.length > 0 && filteredGoods.length < this.goodsFromProps.length) {
          const uniqueGoods = [...new Set(filteredGoods.map(JSON.stringify))].map(JSON.parse);
          uniqueGoods.sort((a, b) => a.Title.localeCompare(b.Title));

          // Сбрасываем и заполняем массивы
          this.showTableData = uniqueGoods;
          this.finalDisplayData = uniqueGoods.slice(0, this.itemsPerLoad);
          this.loadedCount = this.itemsPerLoad;

          this.showSnackbar(`Найдено ${filteredGoods.length} позиций`,
              'mdi-check-circle-outline',
              'success');

          this.enableButtonAfterDelayPlusGoUp();
        } else {
          this.openDialog();
          this.enableButtonAfterDelay();
        }
      }, 300);
    },

    searchGoods() {
      this.disabledBTN();
      this.isItemShow = false;

      setTimeout(() => {
        const searchInputData = this.inputData?.trim();

        if (!searchInputData) {
          this.openDialog();
          this.enableButtonAfterDelay();
          return;
        }

        const searchTerms = [...new Set(
            searchInputData.split(',')
                .map(term => term.trim())
                .filter(term => term)
        )];

        if (!searchTerms.length) {
          this.openDialog();
        } else {
          searchTerms.length === 1
              ? this.performSearch(searchTerms[0], true)
              : this.performSearch(searchTerms, false);
        }
      }, 300);
    },
    performSearch(data, isSingle) {
      const searchCriteria = [
        {key: 'Code', exactMatch: true},
        {key: 'Article', exactMatch: true},
        {key: 'ArtEpicenter', exactMatch: true},
        {key: 'ArtAshan', exactMatch: true},
        {key: 'ArtTavriya', exactMatch: true},
        {key: 'ArtNovus', exactMatch: true},
        {key: 'ArtComfy', exactMatch: true},
        {key: 'Shtrihcode', exactMatch: true},
        {key: 'Title', exactMatch: false}
      ];
      const results = (isSingle ? this.searchSingle(data, searchCriteria) : this.searchMultiple(data));

      if (results.length > 1) {
        const uniqueResults = this.getUniqueSortedResults(results);

        this.showTableData = uniqueResults;
        this.finalDisplayData = uniqueResults.slice(0, this.itemsPerLoad);
        this.loadedCount = this.itemsPerLoad;

        this.showSnackbar(
            `Найдено ${uniqueResults.length} позиций`,
            'mdi-check-circle-outline',
            'success');

        this.enableButtonAfterDelayPlusGoUp();

      } else if (results.length === 1) {
        this.showCurrentItem(results[0]);

        this.enableButtonAfterDelayPlusGoUp();
      } else {
        this.openDialog();
        this.enableButtonAfterDelay();
      }
    },
    searchSingle(data, criteria) {
      const normalizedData = data.toLowerCase();
      return this.goodsFromProps.filter(item =>
          criteria.some(({key, exactMatch}) =>
                  item[key] && (
                      exactMatch ? item[key] === data : item[key].toLowerCase().includes(normalizedData)
                  )
          )
      );
    },
    searchMultiple(data) {
      const searchTerms = new Set(data);
      return this.goodsFromProps.filter(item =>
          Object.keys(item).some(key => searchTerms.has(item[key]))
      );
    },
    getUniqueSortedResults(results) {
      const uniqueMap = new Map();
      results.forEach(item => {
        const uniqueKey = item.Code || item.id || JSON.stringify(item);
        uniqueMap.set(uniqueKey, item);
      });

      return Array.from(uniqueMap.values()).sort((a, b) => a.Title.localeCompare(b.Title));
    },

    enableButtonAfterDelay() {
      setTimeout(() => {
        this.unDisabledBTN();
      }, 200);
    },
    enableButtonAfterDelayPlusGoUp() {
      setTimeout(() => {
        setTimeout(() => {
          this.$refs.tableData.scrollTo({top: 0, left: 0, behavior: 'smooth'});
        }, 1)
        this.unDisabledBTN();
      }, 200);
    },

    showSnackbar(message, resultIcon, resultIconColor) {
      this.snackbarResult = resultIcon;
      this.snackbarResultColor = resultIconColor;
      this.snackbarText = message;
      this.snackbar = true;
    },

    updateWidth() {
      this.screenWidth = window.innerWidth;
    },
    checkScreenWidth() {
      if (this.screenWidth < 961) {
        this.isFilterShow = false;
        this.isRadioResult = false;
      }
    },
    handleEnter(event) {
      if (event.key === 'Enter') {
        this.searchGoods();
      }
    },

    firstGenerationClearData() {
      this.disabledBTN();
      this.$refs.tableData.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth'
      });
      if (this.isItemShow === true) {
        this.isItemShow = false;
      }
      setTimeout(() => {
        this.showTableData = [];
        this.finalDisplayData = [];
        this.loadedCount = 50;
        this.showTableData = [...this.goodsFromProps.slice(-50).sort((a, b) => a.Title.localeCompare(b.Title))];
        this.finalDisplayData = this.showTableData;
      }, 300);
      setTimeout(() => {
        this.showSnackbar(
            `Последние 50 позиций`,
            'mdi-check-circle-outline',
            'success');
        this.enableButtonAfterDelayPlusGoUp();
      }, 400);

    },
    allGenerationData() {
      this.disabledBTN();
      this.$refs.tableData.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth'
      });
      if (this.isItemShow === true) {
        this.isItemShow = false;
      }
      setTimeout(() => {
        this.showTableData = [];
        this.finalDisplayData = [];
        this.loadedCount = 0;
        this.showTableData = [...this.goodsFromProps];

        this.finalDisplayData = this.showTableData.slice(0, this.itemsPerLoad);
        this.loadedCount = this.itemsPerLoad;
        this.finalDisplayData.reverse();
      }, 300);
      setTimeout(() => {
        this.showSnackbar(
            `Найдено ${this.goodsFromProps.length} позиций`,
            'mdi-check-circle-outline',
            'success');
        this.enableButtonAfterDelayPlusGoUp();
      }, 400);
    },
    newGenerationData() {
      this.disabledBTN();
      this.$refs.tableData.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth'
      });
      if (this.isItemShow === true) {
        this.isItemShow = false;
      }
      setTimeout(() => {
        this.showTableData = [];
        this.loadedCount = 0;
        this.showTableData = [...this.goodsFromProps.filter(elem => elem.Status == 'Новинка')];
        this.finalDisplayData = [];
        this.finalDisplayData = this.showTableData.slice(0, this.itemsPerLoad);
        this.loadedCount = this.itemsPerLoad;
      }, 300);
      setTimeout(() => {
        this.showSnackbar(
            `Найдено ${this.goodsFromProps.filter(elem => elem.Status == 'Новинка').length} позиций`,
            'mdi-check-circle-outline',
            'success');
        this.enableButtonAfterDelayPlusGoUp();
      }, 400);
    },

    handleScroll() {
      const table = this.$refs.tableData;
      if (table.scrollTop + table.clientHeight >= table.scrollHeight - 100) {
        this.loadMore();
      }
    },
    loadMore() {
      if (this.loadedCount < this.showTableData.length) {
        const additionalData = this.showTableData.slice(this.loadedCount, this.loadedCount + this.itemsPerLoad);
        this.finalDisplayData.push(...additionalData);
        this.loadedCount += this.itemsPerLoad;
      }
    },

    createFullTableData() {
      this.disabledBTN();
      this.copyTableData = [];
      this.copyTableData = [...this.goodsFromProps];
      this.copyTableData.sort((a, b) => b.Available - a.Available);

      setTimeout(() => {
        this.overlay = !this.overlay;
      }, 300);
      setTimeout(() => {
        this.unDisabledBTN();
      }, 400);
    },
    createCurrentTableData(){
      this.disabledBTN();
      this.copyTableData = [];
      this.copyTableData = [...this.showTableData];
      this.copyTableData.sort((a, b) => b.Available - a.Available);

      setTimeout(() => {
        this.overlay = !this.overlay;
      }, 300);
      setTimeout(() => {
        this.unDisabledBTN();
      }, 400);
    },
    copyTable() {
      let data = this.$refs.copyData;
      if (!navigator.clipboard) {
        this.snackbarText = "Clipboard API не поддерживается";
        this.snackbar = true;
        return;
      }
      if (data) {
        navigator.clipboard.writeText(data.innerText).then(() => {
          this.snackbarText = "Данные скопированы";
          this.snackbar = true;
        }).catch(error => {
          this.snackbarText = `Ошибка! ${error}`;
          this.snackbar = true;
        });
      } else {
        this.snackbarText = "Элемент copyData не найден";
        this.snackbar = true;
      }
    },

    disabledBTN() {
      this.isSearchOrFiltered = true;
      this.firstGenerationButton = false;
      this.allGenerationButton = false;
      this.newGenerationButton = false;
      this.tableGenerationButton = false;
    },
    unDisabledBTN() {
      this.isSearchOrFiltered = false;
      this.firstGenerationButton = true;
      this.allGenerationButton = true;
      this.newGenerationButton = true;
      this.tableGenerationButton = true;
    },

    codeUp() {
      this.showTableData.sort((a, b) => Number(a.Code) - Number(b.Code));
      this.updateSortedData();
    },
    codeDown() {
      this.showTableData.sort((a, b) => Number(b.Code) - Number(a.Code));
      this.updateSortedData();
    },
    articleUp() {
      this.showTableData.sort((a, b) => a.Article.localeCompare(b.Article));
      this.updateSortedData();
    },
    articleDown() {
      this.showTableData.sort((a, b) => b.Article.localeCompare(a.Article));
      this.updateSortedData();
    },
    titleleUp() {
      this.showTableData.sort((a, b) => a.Title.localeCompare(b.Title));
      this.updateSortedData();
    },
    titleleDown() {
      this.showTableData.sort((a, b) => b.Title.localeCompare(a.Title));
      this.updateSortedData();
    },
    availableUp() {
      this.showTableData.sort((a, b) => a.Available - b.Available);
      this.updateSortedData();
    },
    availableDown() {
      this.showTableData.sort((a, b) => b.Available - a.Available);
      this.updateSortedData();
    },
    reserveUp() {
      this.showTableData.sort((a, b) => Number(a.Reserv) - Number(b.Reserv));
      this.updateSortedData();
    },
    reserveDown() {
      this.showTableData.sort((a, b) => Number(b.Reserv) - Number(a.Reserv));
      this.updateSortedData();
    },
    updateSortedData() {
      this.finalDisplayData = [];
      this.finalDisplayData = this.showTableData.slice(0, this.itemsPerLoad);
      this.loadedCount = this.itemsPerLoad;
      this.$refs.tableData.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    },

    getPhotoPath(article) {
      try {
        return require(`@/assets/images/photo-all/images/${article}.webp`);
      } catch (error) {
        try {
          const modifiedArticle = article.replaceAll('/', '-');
          return require(`@/assets/images/photo-all/images/${modifiedArticle}.webp`);
        } catch (error) {
          // console.error(`Image not found: ${article}.webp`);
          return require('@/assets/images/photo-all/images/error.webp');
        }
      }
    },
    operateItem(item) {
      console.log(item)
    },

    openDialog() {
      this.dialog = true;
      setTimeout(() => {
        this.dialog = false;
      }, 3000);
    },

    showCurrentItem(item) {
      this.getReserves(item);
      this.sendItemData = item;
      this.isItemShow = true;
    },
    getReserves(dataItem) {
      let fullReserves = [];
      fullReserves = [...dataStore.getReservData()];

      const uniqueArray = [];
      const uniqueItems = {};

      fullReserves.forEach(item => {
        const key = `${item.CodeReserv}_${item.CompanyReserv}`;

        if (uniqueItems[key]) {
          uniqueItems[key].CountReserv += item.CountReserv;
        } else {
          uniqueItems[key] = {...item};
        }
      });

      for (const key in uniqueItems) {
        uniqueArray.push(uniqueItems[key]);
      }
      this.uniqReserves = [];
      this.uniqReserves = uniqueArray.filter(item => item.CodeReserv == dataItem.Code)
    },
    closeCurrentItem() {
      this.isItemShow = false;
    },

    checkAuth(){
      let isAuthLocal = JSON.parse(localStorage.getItem("sale-auth"));
      if (isAuthLocal.role === 'promoter'){
        this.isPromoter = true;
      }
    }
  },
  watch: {
    screenWidth(newVal) {
      if (newVal < 961) {
        this.isFilterShow = false;
        this.isRadioResult = false;
      } else if (newVal > 960) {
        this.isFilterShow = true;
        this.isRadioResult = true;
      }
    },
    isRadioResult(newValue, oldValue) {
      if (newValue !== oldValue) {
        this.isFilterShow = newValue;
      }
    }
  },
  created() {
    window.addEventListener('resize', this.updateWidth);
  },
  mounted() {
    this.checkAuth();
    this.checkScreenWidth();
    this.tranferGoodsAndReservesFromProps();
    this.createSelectedData();
    document.addEventListener('keydown', this.handleEnter);
    localStorage.setItem('checkLoadStatus', 'loaded');
  },
  beforeUnmount() {
    localStorage.removeItem('checkLoadStatus');
    document.removeEventListener('keydown', this.handleEnter);
    window.removeEventListener('resize', this.updateWidth);
  }
}
</script>

