<template>
  <div class="main-data-container">
    <div class="main-data-load_small" v-if="screenWidth < 961">

      <v-progress-circular class="progress-round"
                           :size="70"
                           width="7"
                           color="blue-darken-2"
                           indeterminate
                           v-if="!isLoad && !isPogress"
      ></v-progress-circular>
      <div class="stepper-load" v-if="isPogress && !isLoad">
        <h3>Загрузка, пожалуйста подождите...</h3>
        <v-progress-linear class="line-prog" color="primary" model-value="20" indeterminate></v-progress-linear>
      </div>

    </div>
    <div class="main-data-load" v-if="screenWidth > 960">
      <v-progress-circular class="progress-round"
                           :size="70"
                           width="7"
                           color="blue-darken-2"
                           indeterminate
                           v-if="!isLoad && !isPogress"
      ></v-progress-circular>
      <div class="stepper-load" v-if="isPogress && !isLoad">
        <h3>Загрузка, пожалуйста подождите...</h3>
        <v-stepper class="progress-stepper" :model-value="['1','2','3','4','5']">
          <v-stepper-header>

            <v-stepper-item style="display: none"></v-stepper-item>

            <v-stepper-item
                title="Остатки"
                value="!"
                :complete="loadYearsData.goods === true"
                :color="loadYearsData.goods ? 'success' : 'danger'"
            ></v-stepper-item>
            <v-divider></v-divider>
            <v-stepper-item
                title="Резервы"
                value="!"
                :complete="loadYearsData.reserves === true"
                :color="loadYearsData.reserves ? 'success' : 'danger'"
            ></v-stepper-item>
            <v-divider></v-divider>
            <v-stepper-item
                title="Отпуска"
                value="!"
                :complete="loadYearsData.weekends === true"
                :color="loadYearsData.weekends ? 'success' : 'danger'"
            ></v-stepper-item>
            <v-divider></v-divider>
            <v-stepper-item
                title="2023"
                value="!"
                :complete="loadYearsData.year23 === true"
                :color="loadYearsData.year23 ? 'success' : 'danger'"
            ></v-stepper-item>
            <v-divider></v-divider>
            <v-stepper-item
                title="2024"
                value="!"
                :complete="loadYearsData.year24 === true"
                :color="loadYearsData.year24 ? 'success' : 'danger'"
            ></v-stepper-item>

          </v-stepper-header>
        </v-stepper>
        <v-progress-linear class="line-prog" color="primary" model-value="20" indeterminate></v-progress-linear>
      </div>
    </div>
    <div class="main-data-body" v-if="isLoad">
      <TableDataComponent/>
    </div>
  </div>
  <div class="text-center">
    <v-snackbar
        v-model="snackbar"
        multi-line
        timeout="3000"
        style="margin-bottom: 60px"
    >
      {{ snackbarText }}

      <template v-slot:actions>
        <v-btn
            color="red"
            variant="text"
            @click="snackbar = false"
        >
          Закрыть
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import TableDataComponent from "@/components/body-search/TableDataComponent.vue";
import {dataStore} from "@/operations/requests";
import $ from "jquery";

export default {
  name: "MainDataComponent",
  components: {
    TableDataComponent,
  },
  data() {
    return {
      screenWidth: window.innerWidth,

      isLoad: false,
      isPogress: null,

      dialog: false,
      snackbar: false,
      snackbarText: '',

      loadYearsData: {year23: false, year24: false, goods: false, reserves: false, weekends: false},
      dataSales2023: [],
      dataSales2024: [],
    }
  },
  methods: {
    async getRequest() {
      this.isPogress = false;
      const [sales2023Module, sales2024Module] = await Promise.all([
        import('@/operations/data-sales-2023'),
        import('@/operations/data-sales-2024')
      ]);

      let checkGoodsLoad = dataStore.getGoodData();
      let checkReservsLoad = dataStore.getReservData();

      if (checkGoodsLoad && checkReservsLoad) {
        setTimeout(() => {
          this.loadYearsData.goods = true;
        }, 500)
        setTimeout(() => {
          this.loadYearsData.reserves = true;
        }, 1000)
        setTimeout(() => {
          this.dataSales2023 = [...sales2023Module.default.getSales_2023()];
          this.loadYearsData.year23 = true;
        }, 1500)
        setTimeout(() => {
          this.dataSales2024 = [...sales2024Module.default.getSales_2024()];
          this.loadYearsData.year24 = true;
        }, 2000)
        setTimeout(() => {
          this.loadYearsData.weekends = true;
        }, 2500)
      } else {
        this.isPogress = true;
        await $.ajax({
          url: 'https://script.google.com/macros/s/AKfycbz0-J2MPZeiKhvsZbYv84p-FjHrMK2bAufHBqQTRtozXg7mEoevKlMZok1Df2dEqUWv/exec',
          type: "GET",
          crossDomain: true,
          dataType: "json",
          success: (response) => {
            dataStore.setGoodData(response.goods);
            this.loadYearsData.goods = true;
          },
          error: (error) => {
            this.snackbarText = 'Ошибка в загрузке остатков'
            this.snackbar = true;
            console.log(error);
          }
        });
        await $.ajax({
          url: 'https://script.google.com/macros/s/AKfycbycgFWyXe4mp_hb5jg48ak4eTcTp3yoUf_IMe_j9NZ84QAJq0KFccakkj3QHySyg07uMQ/exec',
          type: "GET",
          crossDomain: true,
          dataType: "json",
          success: (response) => {
            dataStore.setReservData(response.reservs);
            this.loadYearsData.reserves = true;
          },
          error: (error) => {
            this.snackbarText = 'Ошибка в загрузке резервов'
            this.snackbar = true;
            console.log(error);
          }
        });
        await $.ajax({
          url: 'https://script.google.com/macros/s/AKfycbzC9UsMwUPuJz3egmsswDu6UexRue76zTk40CAUvqOW5iRp0cv4qlpQ5zAdig6OD0Cm/exec',
          type: "GET",
          crossDomain: true,
          dataType: "json",
          success: (response) => {
            dataStore.setWeekendsData(response.weekends);
            this.loadYearsData.weekends = true;
          },
          error: (error) => {
            this.snackbarText = 'Ошибка в загрузке резервов'
            this.snackbar = true;
            console.log(error);
          }
        });
        try {
          const [sales2023Module, sales2024Module] = await Promise.all([
            import('@/operations/data-sales-2023'),
            import('@/operations/data-sales-2024')
          ]);

          this.dataSales2023 = [...sales2023Module.default.getSales_2023()];
          setTimeout(() => {
            this.loadYearsData.year23 = true;
          }, 1000)

          this.dataSales2024 = [...sales2024Module.default.getSales_2024()];
          setTimeout(() => {
            this.loadYearsData.year24 = true;
          }, 2000)
        } catch (error) {
          this.snackbarText = 'Ошибка при загрузке данных продаж';
          this.snackbar = true;
          console.error(error);
        }
      }
    },
    updateWidth() {
      this.screenWidth = window.innerWidth;
    },
  },
  created() {
    window.addEventListener('resize', this.updateWidth);
  },
  mounted() {
    this.getRequest();
  },
  watch: {
    loadYearsData: {
      handler(newVal) {
        if (newVal.year23 && newVal.year24 && newVal.goods && newVal.reserves && newVal.weekends) {
          setTimeout(() => {
            this.isLoad = true;
          }, 1000)
        }
      },
      deep: true
    }
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.updateWidth);
  }
}
</script>

<style lang="scss" scoped>
.main-data-container {

  .main-data-load_small {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 50px;
    padding: 5px;
    margin: 0 auto;

    .progress-round {
      align-self: center;
    }

    .stepper-load {
      background-color: snow;
      padding: 10px 35px 10px 10px;
      max-width: 500px;
      width: 100%;
      margin: 50px auto;
      border-radius: 5px;
      overflow: hidden;
      position: relative;
      z-index: 100;

      &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: -12px;
        width: 50px;

      }

      .line-prog {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
      }

      h3 {
        text-align: center;
        margin: 10px;
        color: rgba(0, 0, 0, 0.6);
        font-size: clamp(1rem, 2.5vw, 2rem);;
      }

      .progress-stepper {
        box-shadow: none;
        background-color: transparent;
      }
    }
  }

  .main-data-load {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 50px;
    padding: 5px;
    margin: 0 auto;

    .progress-round {
      align-self: center;
    }

    .stepper-load {
      background-color: snow;
      padding: 10px 35px 10px 10px;
      max-width: 1000px;
      width: 100%;
      margin: 50px auto;
      border-radius: 5px;
      overflow: hidden;
      position: relative;
      z-index: 100;
      clip-path: polygon(96% 0, 100% 50%, 96% 100%, 0 100%, 0 50%, 0 0);

      &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: -12px;
        width: 50px;

      }

      .line-prog {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
      }

      h3 {
        text-align: left;
        margin: 10px;
        color: rgba(0, 0, 0, 0.6);
      }

      .progress-stepper {
        box-shadow: none;
        background-color: transparent;
      }
    }
  }
}
</style>