<template>
  <div class="item-container" ref="chartData">
    <div class="item-container__header">
      <div class="item__photo" @click="overlay = true">
        <img width="55" height="55" alt="photo" :src="getPhotoPath(currentItem.Article)">
      </div>
      <div class="item__title">{{ currentItem.Title }}</div>
      <v-icon class="item__close-btn" size="x-large" color="red" @click="closeCurrentItem">mdi-close</v-icon>
    </div>
    <div class="item-container__body">
      <div class="body__articles column">
        <div class="section-title">
          <v-icon size="small" color="amber">mdi-barcode</v-icon>
          Коды
        </div>
        <div class="art">
          <div class="info">Код товара:</div>
          <div class="data">{{ currentItem.Code }}</div>
        </div>
        <div class="art">
          <div class="info">Артикул:</div>
          <div class="data">{{ currentItem.Article }}</div>
        </div>
        <div class="art">
          <div class="info">Артикул ЕПЦ:</div>
          <div class="data">{{ currentItem.ArtEpicenter }}</div>
        </div>
        <div class="art">
          <div class="info">Артикул Ашан:</div>
          <div class="data">{{ currentItem.ArtAshan }}</div>
        </div>
        <div class="art">
          <div class="info">Артикул Таврия:</div>
          <div class="data">{{ currentItem.ArtTavriya }}</div>
        </div>
        <div class="art">
          <div class="info">Артикул Новус:</div>
          <div class="data">{{ currentItem.ArtNovus }}</div>
        </div>
        <div class="art">
          <div class="info">Артикул Комфи:</div>
          <div class="data">{{ currentItem.ArtComfy }}</div>
        </div>
        <div class="art">
          <div class="info">Штрихкод:</div>
          <div class="data">{{ currentItem.Shtrihcode }}</div>
        </div>
        <div class="art">
          <div class="info">Код УКТЗ:</div>
          <div class="data">{{ currentItem.Uktz }}</div>
        </div>
      </div>
      <div class="body__info-section column">
        <div class="section-title">
          <v-icon size="small" color="amber">mdi-cash-multiple</v-icon>
          Общее и цены
        </div>
        <div class="art">
          <div class="info">Торговая марка:</div>
          <div class="data">{{ currentItem.Tm }}</div>
        </div>
        <div class="art">
          <div class="info">Ед.измерения:</div>
          <div class="data">{{ currentItem.Unit }}</div>
        </div>
        <div class="art">
          <div class="info">Страна:</div>
          <div class="data">{{ currentItem.Country }}</div>
        </div>
        <div class="art">
          <div style="align-self: center" class="info">Материал:</div>
          <div class="data">{{ currentItem.Material }}</div>
        </div>
        <div class="art">
          <div class="info">Категория:</div>
          <div class="data">{{ currentItem.Kategori }}</div>
        </div>
        <div class="art">
          <div class="info">Коллекция:</div>
          <div class="data">{{ currentItem.Kollection }}</div>
        </div>
        <div class="art">
          <div class="info">Цена(база):</div>
          <div class="data">{{ currentItem.PriceBase + ' ' + 'UAH' }}</div>
        </div>
        <div class="art">
          <div class="info">Цена РРЦ:</div>
          <div class="data">{{ Number(currentItem.PriseRrc).toFixed(2).toString() + ' ' + 'UAH' }}</div>
        </div>
        <div class="art">
          <div class="info">Цена РРЦ (акц):</div>
          <div class="data">{{ currentItem.PriseRrcPromo + ' ' + 'UAH' }}</div>
        </div>
        <div class="art">
          <div class="info">Скидка:</div>
          <div style="color: red" class="data">{{ (currentItem.PriceDiscount * 100).toFixed() + '' + '%' }}</div>
        </div>
      </div>
      <div class="body__available-section column">
        <div class="section-title">
          <v-icon size="small" color="amber">mdi-ruler</v-icon>
          Размеры и остатки
        </div>
        <div class="art">
          <div class="info">Длинна(см):</div>
          <div class="data">{{ currentItem.LengthSm }}</div>
        </div>
        <div class="art">
          <div class="info">Ширина(см):</div>
          <div class="data">{{ currentItem.WidthSm }}</div>
        </div>
        <div class="art">
          <div class="info">Высота(см):</div>
          <div class="data">{{ currentItem.HeightSm }}</div>
        </div>
        <div class="art">
          <div class="info">Диаметр(см):</div>
          <div class="data">{{ currentItem.DiametrSm }}</div>
        </div>
        <div class="art">
          <div class="info">Вес(кг):</div>
          <div class="data">{{ currentItem.WeightKg }}</div>
        </div>
        <div class="art">
          <div class="info">Штук в коробке:</div>
          <div class="data">{{ currentItem.CountBox }}</div>
        </div>
        <div class="art">
          <div class="info">Склад:</div>
          <div class="data" :style="{'color': currentItem.Available > 0 ? 'green' : 'red'}">
            {{ currentItem.Available }}
          </div>
        </div>
        <div class="art">
          <div class="info">ДПМ:</div>
          <div class="data" :style="{'color': currentItem.AvailableDpm > 0 ? 'green' : 'red'}">
            {{ currentItem.AvailableDpm }}
          </div>
        </div>
        <div class="art">
          <div class="info">2320:</div>
          <div class="data" :style="{'color': currentItem.Available2320 > 0 ? 'green' : 'red'}">
            {{ currentItem.Available2320 }}
          </div>
        </div>
        <div class="art">
          <div class="info">Резерв:</div>
          <div class="data" :style="{'color': currentItem.Reserv > 0 ? 'orange' : 'red'}">{{ currentItem.Reserv }}</div>
        </div>
      </div>
    </div>
    <div class="item-container__footer">

      <div class="footer-left">
        <v-chip class="chip-akcion"
                v-if="currentItem.Akcion != '-'"
                variant="elevated"
                size="x-small"
                color="deep-purple-lighten-1"
                label>
          <v-icon icon="mdi-spellcheck" start></v-icon>
          {{currentItem.Akcion}}
        </v-chip>
        <v-chip class="chip-categor"
                v-if="currentItem.Categor != '-'"
                variant="elevated"
                size="x-small"
                color="cyan-darken-1"
                label>
          <v-icon icon="mdi-file-document" start></v-icon>
          {{currentItem.Categor}}
        </v-chip>
      </div>

      <div class="footer-right">
        <v-chip class="chip-order"
                v-if="currentItem.OrderCount != '-' && currentItem.OrderData != '-'"
                variant="elevated"
                size="x-small"
                color="orange-darken-2"
                label>
          <v-icon icon="mdi-bus" start></v-icon>
          {{Number(currentItem.OrderCount).toLocaleString()}} шт. / до {{currentItem.OrderData}}
        </v-chip>
        <v-chip class="chip-new"
                v-if="currentItem.Status == 'Новинка'"
                variant="elevated"
                size="x-small"
                color="pink"
                label>
          <v-icon icon="mdi-tag" start></v-icon>
          Новинка
        </v-chip>
        <v-chip class="chip-stock"
                v-if="currentItem.Stock == 'Сток'"
                variant="elevated"
                size="x-small"
                color="yellow"
                label>
          <v-icon icon="mdi-cube-outline" start></v-icon>
          Сток
        </v-chip>
        <v-chip class="chip-permanent"
                v-if="currentItem.Status != 'Новинка' && currentItem.Stock != 'Сток'"
                variant="elevated"
                size="x-small"
                color="green-lighten-4"
                label>
          <v-icon icon="mdi-cart-outline" start></v-icon>
          Перманент
        </v-chip>
      </div>

    </div>
    <div class="item-reserves__body" v-if="currentRole !== 'promoter'">
      <div class="item-container__reserves column fake-element"></div>
      <div class="item-container__reserves column">
        <div class="section-title">
          <v-icon size="small" color="red-accent-3">mdi-alert-outline</v-icon>
          Резервы
        </div>
        <h3 v-if="currentReserves.length == 0">НЕТ РЕЗЕРВОВ</h3>
        <div class="item__reserves-body" v-if="currentReserves.length > 0">
          <div class="art" v-for="reserv of currentReserves" :key="reserv.CompanyReserv">
            <div class="info">{{ reserv.CompanyReserv }}</div>
            <div class="data">{{ reserv.CountReserv }}</div>
          </div>
        </div>
      </div>
      <div class="item-container__reserves column fake-element"></div>
    </div>
    <div class="divider" v-if="currentRole !== 'promoter'"></div>
    <div class="item-container__sales" v-if="currentRole !== 'promoter'">
      <div class="sales__header">
        <div class="sales__years-controls">
          <v-btn class="sales-all-btn"
                 color="teal"
                 size="small"
                 @click="getSalesAllYearsByMonth"
          >
            <v-icon style="margin-right: 3px">mdi-chart-bar</v-icon>
            <span>Все года</span></v-btn>
          <v-btn class="sales-2024-btn"
                 color="teal"
                 size="small"
                 @click="getSales2024ByMonth"
          >
            <v-icon style="margin-right: 3px">mdi-chart-bar</v-icon>
            <span>2024</span></v-btn>
          <v-btn class="sales-2023-btn"
                 color="teal"
                 size="small"
                 @click="getSales2023ByMonth"
          >
            <v-icon style="margin-right: 3px">mdi-chart-bar</v-icon>
            <span>2023</span></v-btn>
        </div>
        <div class="sales__company-controls">
          <v-select
              v-model="choosedCompanyForChart"
              :items="companyForChart"
              label="Контрагент"
              persistent-hint
              density="compact"
              color="teal"
              class="select-company"
              variant="underlined"
              clearable
          ></v-select>
          <v-btn class="company-all-btn"
                 color="blue-grey"
                 size="small"
                 @click="getSalesFullYearsByCompany"
          >
            <v-icon style="margin-right: 3px">mdi-chart-areaspline</v-icon>
            <span>Все года</span></v-btn>
          <v-btn class="company-2024-btn"
                 color="blue-grey"
                 size="small"
                 @click="getSales2024ByCompany"
          >
            <v-icon style="margin-right: 3px">mdi-chart-areaspline</v-icon>
            <span>2024</span></v-btn>
          <v-btn class="company-2023-btn"
                 color="blue-grey"
                 size="small"
                 @click="getSales2023ByCompany"
          >
            <v-icon style="margin-right: 3px">mdi-chart-areaspline</v-icon>
            <span>2023</span></v-btn>
        </div>
      </div>
      <div class="sales__body">
        <div class="sales__body-chart" v-show="isSales">
          <canvas ref="myChart2024" class="item-chart"></canvas>
        </div>
        <div class="sales__not-found" v-show="!isSales">
          <h2>Отгрузок не было...</h2>
          <div class="not-found__photo">
            <img src="../../assets/images/photo-png/not-found.png" alt="">
          </div>
        </div>

      </div>
    </div>
  </div>
  <v-overlay class="overlay-for-data" style="background-color: rgba(0,0,0,0.7)" v-model="overlay">
    <v-sheet
        class="pa-4 text-center mx-auto sheet-for-overlay"
        elevation="12"
        rounded="lg"
    >
      <a :href="getPhotoPath(currentItem.Article)" download>
        <v-icon class="sheet-buttons-btn-dovnload" color="success" size="x-large" @click="console.log('Скачать')">
          mdi-download
        </v-icon>
      </a>
      <v-icon class="sheet-buttons-btn-close" color="red" size="x-large" @click="overlay = !overlay">mdi-close</v-icon>

      <img alt="photo" :src="getPhotoPath(currentItem.Article)">
    </v-sheet>
  </v-overlay>
</template>

<script>
import {dataStore} from "@/operations/requests";
import {Chart, registerables} from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';
Chart.register(...registerables, ChartDataLabels);

export default {
  name: "ItemComponent",
  props: {
    currentItem: Object,
    currentReserves: Array,
    isVisible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      overlay: false,
      currentRole: null,

      monthForChart: [
        "Январь",
        "Февраль",
        "Март",
        "Апрель",
        "Май",
        "Июнь",
        "Июль",
        "Август",
        "Сентябрь",
        "Октябрь",
        "Ноябрь",
        "Декабрь"
      ],
      companyForChart: [],
      choosedCompanyForChart: null,
      isSales: false,

      sales2023: [],
      sales2024: [],
      fullReserves: [],
      uniqReserves: [],
    }
  },
  methods: {
    checkRole() {
      let role = JSON.parse(localStorage.getItem("sale-auth"));
      this.currentRole = role.role;
    },
    handleEscape(event) {
      if (event.key === 'Escape') {
        if (!this.overlay) {
          this.closeCurrentItem();
        } else {
          this.overlay = false;
        }
      }
    },
    dataStore() {
      this.fullReserves = [...dataStore.getReservData()];
    },
    getPhotoPath(article) {
      try {
        return require(`@/assets/images/photo-all/images/${article}.webp`);
      } catch (error) {
        try {
          const modifiedArticle = article.replaceAll('/', '-');
          return require(`@/assets/images/photo-all/images/${modifiedArticle}.webp`);
        } catch (error) {
          return require('@/assets/images/photo-all/images/error.webp');
        }
      }
    },
    async loadAllSales() {
      const [sales2023Module, sales2024Module] = await Promise.all([
        import('@/operations/data-sales-2023'),
        import('@/operations/data-sales-2024')
      ]);

      this.sales2023 = [...sales2023Module.default.getSales_2023()];
      this.sales2024 = [...sales2024Module.default.getSales_2024()];

      this.getAllCompanies();

      //let find23 = this.sales2023.filter(item => item.CodeSale == toRaw(this.currentItem.Code)); //Result 2023
      //let find24 = this.sales2024.filter(item => item.CodeSale == toRaw(this.currentItem.Code)); //Result 2024
    },
    getAllCompanies() {
      let unicConcatCompany = [
        ...new Set(
            [...this.sales2024, ...this.sales2023]
                .map(item => item.CompanySale)
        )
      ].sort((a, b) => a.localeCompare(b));

      this.companyForChart = unicConcatCompany;
    },
    closeCurrentItem() {
      if (this.chart) {
        this.chart.destroy();
        this.chart = null; // Сбрасываем объект графика
      }
      this.$emit('closeItem', 'back');
    },

    updateChart(){
      if (this.chart) {
        this.chart.update();
      }
    },

// Функция для подсчета суммы продаж по месяцам:
    getSalesByMonthForCode(productCode, data) {
      return data
          .filter(item => item.CodeSale == productCode)
          .reduce((result, item) => {
            const existing = result.find(entry => entry.month == item.MonthSale);
            if (existing) {
              existing.totalSales += item.CountSale;
            } else {
              result.push({month: item.MonthSale, totalSales: item.CountSale});
            }
            return result;
          }, []);
    },
    async getSalesAllYearsByMonth() {
      await this.loadAllSales();

      const salesByMonth2024 = this.getSalesByMonthForCode(this.currentItem.Code, this.sales2024);
      const salesByMonth2023 = this.getSalesByMonthForCode(this.currentItem.Code, this.sales2023);

      function getResultByMonth(data, months) {
        return months.map(month => {
          const sales = data.find(el => el.month === month);
          return sales ? sales.totalSales : 0;
        });
      }

      let resultByMonth2024 = getResultByMonth(salesByMonth2024, this.monthForChart);
      let resultByMonth2023 = getResultByMonth(salesByMonth2023, this.monthForChart);
      let resultByAllYears = resultByMonth2024.map((sales, index) => sales + resultByMonth2023[index]);
      const totalSalesSum = resultByAllYears.reduce((sum, item) => sum + item, 0);

      if (totalSalesSum < 1) {
        this.isSales = false;
      } else if (totalSalesSum > 0) {
        this.isSales = true;
        if (this.chart) {
          this.chart.destroy();
        }

        this.showChartAllYears(resultByAllYears, totalSalesSum);
      }
    },
    getSales2024ByMonth() {
      const salesByMonth = this.getSalesByMonthForCode(this.currentItem.Code, this.sales2024);

      let resultByMonth = [];
      for (let i = 0; i < this.monthForChart.length; i++) {
        const sales = salesByMonth.find(el => el.month === this.monthForChart[i]);
        resultByMonth.push(sales ? sales.totalSales : 0);
      }
      const totalSalesSum = resultByMonth.reduce((sum, item) => sum + item, 0);

      if (totalSalesSum < 1) {
        this.isSales = false;
      } else if (totalSalesSum > 0) {
        this.isSales = true;
        if (this.chart) {
          this.chart.destroy();
        }
        this.showChart2024(resultByMonth, totalSalesSum)
      }
    },
    getSales2023ByMonth() {
      const salesByMonth = this.getSalesByMonthForCode(this.currentItem.Code, this.sales2023);

      let resultByMonth = [];
      for (let i = 0; i < this.monthForChart.length; i++) {
        const sales = salesByMonth.find(el => el.month === this.monthForChart[i]);
        resultByMonth.push(sales ? sales.totalSales : 0);
      }
      const totalSalesSum = resultByMonth.reduce((sum, item) => sum + item, 0);

      if (totalSalesSum < 1) {
        this.isSales = false;
      } else if (totalSalesSum > 0) {
        this.isSales = true;
        if (this.chart) {
          this.chart.destroy();
        }
        this.showChart2023(resultByMonth, totalSalesSum)
      }
    },

    showChartAllYears(salesData, totalCount) {
      let title = 'Штук';
      const ctx = this.$refs.myChart2024.getContext('2d');
      const gradient = ctx.createLinearGradient(0, 0, 0, 400); // Вертикальный градиент
      gradient.addColorStop(0, '#00897B'); // Цвет в начале
      gradient.addColorStop(1, '#4DB6AC'); // Цвет в конце

      this.chart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: this.monthForChart,
          datasets: [{
            label: title,
            data: salesData,
            backgroundColor: gradient,
            borderWidth: 1
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: false,
            subtitle: {
              display: true,
              text: `Суммарные отгрузки за 2024 и 2023 год в разрезе месяцов (${totalCount.toLocaleString()} шт.)`,
              font: {
                weight: 'bold',
                size: '15'
              },
              padding: {
                top: 5,
                bottom: 35 // Отступ снизу текста
              }
            },
            datalabels: {
              color: '#00897B', // Цвет текста
              anchor: 'end', // Расположение метки
              align: 'top', // Выравнивание метки
              formatter: (value) => value !== 0 ? value.toLocaleString() : ''
            }
          },
          scales: {
            y: {
              beginAtZero: true
            },
            x: {
              grid: {
                display: false, // Отключение сетки по оси X
              },
            }
          }
        },
      });
    },
    showChart2024(salesData, totalCount) {
      let title = `Штук`;
      const ctx = this.$refs.myChart2024.getContext('2d');
      const gradient = ctx.createLinearGradient(0, 0, 0, 400); // Вертикальный градиент
      gradient.addColorStop(0, '#00897B'); // Цвет в начале
      gradient.addColorStop(1, '#4DB6AC'); // Цвет в конце

      this.chart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: this.monthForChart,
          datasets: [{
            label: title,
            data: salesData,
            backgroundColor: gradient,
            borderWidth: 1
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: false,
            subtitle: {
              display: true,
              text: `Отгрузки за 2024 год в разрезе месяцов (${totalCount.toLocaleString()} шт.)`,
              font: {
                weight: 'bold',
                size: '15'
              },
              padding: {
                top: 5,
                bottom: 35 // Отступ снизу текста
              }
            },
            datalabels: {
              color: '#00897B', // Цвет текста
              anchor: 'end', // Расположение метки
              align: 'top', // Выравнивание метки
              formatter: (value) => value !== 0 ? value.toLocaleString() : ''
            }
          },
          scales: {
            y: {
              beginAtZero: true
            },
            x: {
              grid: {
                display: false, // Отключение сетки по оси X
              },
            }
          }
        },
      });
    },
    showChart2023(salesData, totalCount) {
      let title = `Штук`;
      const ctx = this.$refs.myChart2024.getContext('2d');
      const gradient = ctx.createLinearGradient(0, 0, 0, 400); // Вертикальный градиент
      gradient.addColorStop(0, '#00897B'); // Цвет в начале
      gradient.addColorStop(1, '#4DB6AC'); // Цвет в конце

      this.chart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: this.monthForChart,
          datasets: [{
            label: title,
            data: salesData,
            backgroundColor: gradient,
            borderWidth: 1
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: false,
            subtitle: {
              display: true,
              text: `Отгрузки за 2023 год в разрезе месяцов (${totalCount.toLocaleString()} шт.)`,
              font: {
                weight: 'bold',
                size: '15'
              },
              padding: {
                top: 5,
                bottom: 35 // Отступ снизу текста
              }
            },
            datalabels: {
              color: '#00897B', // Цвет текста
              anchor: 'end', // Расположение метки
              align: 'top', // Выравнивание метки
              formatter: (value) => value !== 0 ? value.toLocaleString() : ''
            }
          },
          scales: {
            y: {
              beginAtZero: true
            },
            x: {
              grid: {
                display: false, // Отключение сетки по оси X
              },
            }
          }
        },
      });
    },

    // Функция для подсчета суммы продаж по компаниям и месяцам:
    getSalesByCompanyAndMonthForCode(productCode, data) {
      return data
          .filter(item => item.CodeSale == productCode)
          .reduce((result, item) => {
            const existing = result.find(entry => entry.company == item.CompanySale && entry.month == item.MonthSale);
            if (existing) {
              existing.totalSales += item.CountSale;
            } else {
              result.push({company: item.CompanySale, month: item.MonthSale, totalSales: item.CountSale});
            }
            return result;
          }, []);
    },
    async getSalesFullYearsByCompany() {
      let allSales = [...this.sales2024, ...this.sales2023];
      const salesByCompanyAndMonth = this.getSalesByCompanyAndMonthForCode(this.currentItem.Code, allSales);

      if (!this.choosedCompanyForChart) {
        const totalSalesArray = this.companyForChart.map(company =>
            salesByCompanyAndMonth.reduce((sum, elem) =>
                elem.company === company ? sum + elem.totalSales : sum, 0)
        );
        const totalSum = totalSalesArray.reduce((sum, value) => sum + value, 0);

        if (totalSalesArray.every(value => value === 0)) {
          this.isSales = false;
        } else {
          this.isSales = true;
          if (this.chart) {
            this.chart.destroy();
            this.chart = null;
          }
          let text = `Суммарные отгрузки за 2024 и 2023 год в разрезе сетей (${totalSum.toLocaleString()} шт.)`
          this.showChartAllYearsCompany(totalSalesArray, this.companyForChart, text);
        }
      } else {
        const totalSalesByMonth = this.monthForChart.map(month =>
            salesByCompanyAndMonth.reduce((sum, item) =>
                item.company === this.choosedCompanyForChart && item.month === month
                    ? sum + item.totalSales : sum, 0)
        );
        const totalSalesSum = totalSalesByMonth.reduce((sum, value) => sum + value, 0);

        if (totalSalesSum === 0) {
          this.isSales = false;
        } else {
          this.isSales = true;
          if (this.chart) {
            this.chart.destroy();
            this.chart = null;
          }
          let text = `Отгрузки в сети ${this.choosedCompanyForChart} за 2024 и 2023 год в разрезе месяцов (${totalSalesSum.toLocaleString()} шт.)`
          this.showChartAllYearsCompany(totalSalesByMonth, this.monthForChart, text);
        }
      }
    },
    getSales2024ByCompany() {
      let allSales = [...this.sales2024];
      const salesByCompanyAndMonth = this.getSalesByCompanyAndMonthForCode(this.currentItem.Code, allSales);

      if (!this.choosedCompanyForChart) {
        const totalSalesArray = this.companyForChart.map(company =>
            salesByCompanyAndMonth.reduce((sum, elem) =>
                elem.company === company ? sum + elem.totalSales : sum, 0)
        );
        const totalSum = totalSalesArray.reduce((sum, value) => sum + value, 0);

        if (totalSalesArray.every(value => value === 0)) {
          this.isSales = false;
        } else {
          this.isSales = true;
          if (this.chart) {
            this.chart.destroy();
            this.chart = null;
          }
          let text = `Суммарные отгрузки за 2024 год в разрезе сетей (${totalSum.toLocaleString()} шт.)`
          this.showChartAllYearsCompany(totalSalesArray, this.companyForChart, text);
        }
      } else {
        const totalSalesByMonth = this.monthForChart.map(month =>
            salesByCompanyAndMonth.reduce((sum, item) =>
                item.company === this.choosedCompanyForChart && item.month === month
                    ? sum + item.totalSales : sum, 0)
        );
        const totalSalesSum = totalSalesByMonth.reduce((sum, value) => sum + value, 0);

        if (totalSalesSum === 0) {
          this.isSales = false;
        } else {
          this.isSales = true;
          if (this.chart) {
            this.chart.destroy();
            this.chart = null;
          }
          let text = `Отгрузки в сети ${this.choosedCompanyForChart} за 2024 год в разрезе месяцов (${totalSalesSum.toLocaleString()} шт.)`
          this.showChartAllYearsCompany(totalSalesByMonth, this.monthForChart, text);
        }
      }
    },
    getSales2023ByCompany() {
      let allSales = [...this.sales2023];
      const salesByCompanyAndMonth = this.getSalesByCompanyAndMonthForCode(this.currentItem.Code, allSales);

      if (!this.choosedCompanyForChart) {
        const totalSalesArray = this.companyForChart.map(company =>
            salesByCompanyAndMonth.reduce((sum, elem) =>
                elem.company === company ? sum + elem.totalSales : sum, 0)
        );
        const totalSum = totalSalesArray.reduce((sum, value) => sum + value, 0);

        if (totalSalesArray.every(value => value === 0)) {
          this.isSales = false;
        } else {
          this.isSales = true;
          if (this.chart) {
            this.chart.destroy();
            this.chart = null;
          }
          let text = `Суммарные отгрузки за 2023 год в разрезе сетей (${totalSum.toLocaleString()} шт.)`
          this.showChartAllYearsCompany(totalSalesArray, this.companyForChart, text);
        }
      } else {
        const totalSalesByMonth = this.monthForChart.map(month =>
            salesByCompanyAndMonth.reduce((sum, item) =>
                item.company === this.choosedCompanyForChart && item.month === month
                    ? sum + item.totalSales : sum, 0)
        );
        const totalSalesSum = totalSalesByMonth.reduce((sum, value) => sum + value, 0);

        if (totalSalesSum === 0) {
          this.isSales = false;
        } else {
          this.isSales = true;
          if (this.chart) {
            this.chart.destroy();
            this.chart = null;
          }
          let text = `Отгрузки в сети ${this.choosedCompanyForChart} за 2023 год в разрезе месяцов (${totalSalesSum.toLocaleString()} шт.)`
          this.showChartAllYearsCompany(totalSalesByMonth, this.monthForChart, text);
        }
      }
    },

    showChartAllYearsCompany(salesData, currentLables, message) {
      let title = 'Штук';
      const ctx = this.$refs.myChart2024.getContext('2d');
      const gradient = ctx.createLinearGradient(0, 0, 0, 400); // Вертикальный градиент
      gradient.addColorStop(0, '#607D8B'); // Цвет в начале
      gradient.addColorStop(1, '#90A4AE'); // Цвет в конце
      this.chart = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: currentLables,
          datasets: [{
            label: title,
            data: salesData,
            backgroundColor: gradient,
            borderWidth: 1
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: false,
            subtitle: {
              display: true,
              text: message,
              font: {
                weight: 'bold',
                size: '15'
              },
              padding: {
                top: 5,
                bottom: 35 // Отступ снизу текста
              }
            },
            datalabels: {
              color: '#607D8B', // Цвет текста
              anchor: 'end', // Расположение метки
              align: 'top', // Выравнивание метки
              formatter: (value) => value !== 0 ? value.toLocaleString() : ''
            }
          },
          scales: {
            y: {
              beginAtZero: true
            },
            x: {
              grid: {
                display: false, // Отключение сетки по оси X
              },
            }
          }
        },
      });
    },
  },
  watch: {
    isVisible(newValue) {
      if (newValue) {
        this.checkRole();
        if (this.currentRole !== 'promoter') {
          this.getSalesAllYearsByMonth();
          this.choosedCompanyForChart = null;
        }
      }
    }
  },
  mounted() {
    this.checkRole();
    if (this.currentRole !== 'promoter') {
      this.dataStore();
      window.addEventListener('resize', this.updateChart);
    }
    document.addEventListener('keydown', this.handleEscape);
  },
  beforeUnmount() {
    document.removeEventListener('keydown', this.handleEscape);
    document.removeEventListener('resize', this.updateChart);
  }
}
</script>

